/* ------ */
/* GLOBAL */
/* ------ */

.App {
  text-align: center;
  background: radial-gradient(#FD297B, #FF5864, #FF655B);
  font-size: 62.5%;
}

body {
  font-family: 'ABeeZee', sans-serif;
}

img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

/* ------------ */
/* INSTRUCTIONS */
/* ------------ */

.instruction {
  padding: 20px 0 20px 0;
  max-width: 800px;
  margin: 0 auto;
}

.instruction h3 {
  font-size: 2rem;
}

.instruction p {
  font-size: 1.1rem;
}

/* ----------------------- */
/* CONTAINER GENERAL STYLE */
/* ----------------------- */

.mainContainer,
.chatContainer,
.matchContainer {
  border: 5px solid lightgrey;
  border-radius: 30px;
  background: white;
  width: 400px;
  margin: 0 auto;
  padding: 15px 0;
}


/* -------- */
/* MAIN APP */
/* -------- */

nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: #FF5864;
  font-size: 1.3rem;
  font-family: 'Amaranth', sans-serif;
  margin: 20px 0;
  font-size: 2rem;
}

.imageContainer {
  width: 350px;
  height: 500px;
  margin: 0 auto;
}

/* ------- */
/* MATCHES */
/* ------- */

.matchContainer {
  border: 5px solid lightgrey;
  border-radius: 30px;
  width: 400px;
  height: 750px;
  margin: 0 auto;
  background: white;
}

.matchContainer nav {
  font-size: 1.8rem;
}

.topBar {
  display: flex;
  justify-content: space-evenly;
  list-style: none;
  margin: 20px 0;
}

.topBar li:first-child{
  color: #fbc50e;
}

.topBar li:first-child img {
  border: 4px solid #fbc50e;
  font-weight: 800;
}

.matchContainer h2 {
  text-align: left;
  margin-left: 20px;
  color:  #FF5864;
  font-size: 1.2rem;
}

.matchContainer h3 {
  font-size: 1rem;
  font-weight: 800;
}

.matchContainer p {
  font-size: 1rem;
  text-align: left;
  margin-top: 10px;
}

.circleImage img {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  object-fit: cover;
}

.match {
  display: flex;
  margin: 25px 0px 0px 20px;
}

.matchText {
  margin-left: 10px;
}

.messages a,
.messages a:visited {
  text-decoration: none;
  color: black;
}


/* ------------ */
/*     CHAT     */
/* ------------ */

.chatContainer {
  border: 5px solid lightgrey;
  border-radius: 30px;
  width: 400px;
  min-height: 700px;
  margin: 0 auto;
  background: white;
}

.chatContainer a {
  color: lightgrey;
}

.dogTalk {
  display: flex;
  margin-left: 20px;
}

.whatDogSaid {
  background-color: lightgrey;
  border-radius: 20px 20px 20px 0;
  margin: 0 0 20px 10px;
  max-width: 150px;
  padding: 10px 20px;
}

.sentMessage {
  margin-bottom: 180px;
}

.sentMessage li {
  background-color: #5FC9F8;
  max-width: 250px;
  border-radius: 20px 20px 0 20px;
  margin-bottom: 20px;
  margin-left: 120px;
  list-style: none;
  min-width: 150px;
  padding: 15px;
}

.whatYouSay {
  border-top: 2px solid lightgrey;
}

.whatYouSay input {
  width: 280px;
  margin: 15px 0;
  padding: 10px 0;
}
.sendButton {
  font-weight: 800;
}

/* ------- */
/* BUTTONS */
/* ------- */

/* nav button */
.setting,
.chatButton,
.paw,
.report,
.sendButton,
.navButton {
  background: none;
  border: none;
  color: lightgrey;
  font-size: 2rem;
}

/* like/dislike button */
.like,
.dislike {
  color: white;
  border: none;
  margin: 20px 50px;
  border-radius: 50%;
  width: 70px;
  height: 70px;
  font-size: 3rem;
}

.like {
  background-color: #62ECA7;
}

.dislike {
  background-color: #FD297B;
}

/* ------------- */
/* MEDIA QUERIES */
/* ------------- */

@media (max-width: 450px) {

  body {
    overflow-x: hidden;
  }

  /* main */
  .mainContainer,
  .chatContainer,
  .matchContainer {
  border: 5px solid lightgrey;
  border-radius: 30px;
  background: white;
  width: 300px;
  margin: 0 auto;
  padding: 15px 0;
  }

  .imageContainer {
    width: 250px;
    height: 400px;
    margin: 0 auto;
  }

  .like,
  .dislike {
    color: white;
    border: none;
    margin: 10px 30px;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    font-size: 1.5rem;
}

  /* matches */
  .circleImage img {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    object-fit: cover;
  }

  /* message */
  .sentMessage {
    margin-bottom: 150px;
  }
  .sentMessage li {
    margin: 15px 0 0 30px;
  }
}


